.work {
  .info-section {
    width: 20%;
  }
  .content-area {
    background: $color3;
    margin-left: 20%;
    width: 80%;
    min-width: 300px;
    background: white;
    height: 100%;
    overflow: hidden;

    .work-section {
      margin-bottom: 56px;
      h1 {
        font-weight: 700;
        font-size: 2rem;
        text-transform: uppercase;
        margin-bottom: 56px;
      }
      h5 {
        font-weight: 500;
        font-size: 1.3rem;
        margin-bottom: 1.4rem;
      }
      p {
        font-weight: 300;
        font-size: 1rem;
        margin-bottom: 1rem;
      }

      .project-section {
        display: grid;
        grid-template-columns: 1fr 1fr;
        //max-width: 1000px;
        .project-box {
          position: relative;
          cursor: pointer;
          padding: 30px;
          transition: all 0.5s ease-in-out;
          -webkit-background-size: cover;
          -moz-background-size: cover;
          -o-background-size: cover;
          background-size: cover;
          height: 400px;

          &:hover {
            -webkit-box-shadow: 0px 20px 74px 6px rgba(0, 0, 0, 0.12);
            -moz-box-shadow: 20px 0px 74px 6px rgba(0, 0, 0, 0.12);
            box-shadow: 0px 20px 74px 6px rgba(0, 0, 0, 0.12);
          }

          .grey-bg {
            background: #efefef;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            opacity: 0;
            transition: all 0.3s ease-in-out;
            overflow: hidden;
            &:hover {
              opacity: 0.9;
              .project-info {
                .project-details {
                  transform: translate3d(0px, 0, 0);
                  opacity: 1;
                }
                .project-title {
                  transform: translate3d(0, 0, 0);
                  opacity: 1;
                }
              }
            }
            .project-info {
              padding: 56px;
              position: absolute;
              bottom: 0;
              width: 100%;
              .project-details {
                position: relative;
                font-size: 1.1rem;
                text-transform: capitalize;
                margin: 0 0 1rem;
                transform: translate3d(-200px, 0, 0);
                opacity: 0;
                transition: all 0.3s ease-in;
              }
              .project-title {
                font-size: 1.8rem;
                font-weight: 700;
                text-transform: uppercase;
                transform: translate3d(-100px, 0, 0);
                opacity: 0;
                transition: all 0.3s ease-in 0.1s;
              }
              .fa-long-arrow-right {
                position: absolute;
                bottom: 56px;
                right: 56px;
                font-size: 3rem;
              }
            }
          }
        }
      }
    }
  }
}

//phone;
@media (min-width: 0px) and (max-width: 767px) {
  .contact {
    .menu-icon {
      top: -3px;
      color: white;
      .fa {
        color: white;
      }
    }
    .content-area {
      height: auto;
      &:after {
        content: '';
        display: table;
        clear: both;
      }
    }
    .info-section {
      padding: 10px 20px;
      position: static;
      .logo {
        height: 50px;
        width: 70px;
        font-size: 1rem;
      }
      .info-box {
        display: none;
      }
    }
    .content-area {
      margin-left: 0;
      width: 100%;
      .img-container {
        width: 100%;
        padding: 56px 20px 0;
        // position: absolute;
        height: auto;
        .form-section {
          position: static;
          width: 100%;
          height: auto;
          overflow: auto;
        }
      }
      .contact-section {
        width: 70%;
        float: left;
        padding: 20% 20px 0;
        max-width: 800px;
      }
    }
  }
}
